import React, { Component } from "react";
import { authActions } from "./Redux/Actions";
import { connect } from "react-redux";
import IndexRoutes from "./Config/Routes";
import "./App.css";
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: props.user
    };
  }

  componentDidMount() {
  }

  componentWillReceiveProps(nextProps, preProps) {
    console.log('Login props==>',nextProps)
    this.setState({ user: nextProps.user,...nextProps });
  }

  render() {
    return (
      <div className="App">
        <IndexRoutes/>
      </div>
    );
  }
}

//THis Function will get the updated store
const mapStateToProps = state => {
  console.log(state);
  return {
    user: state.authReducers.user
  };
};

const mapDispatchToProps = dispatch => {
  return {
    updateUser: user => dispatch(authActions.updateUser(user)),
    removeUser: () => dispatch(authActions.removeUser())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
