import React from "react";
import { connect } from "react-redux";
import { authActions } from "../../Redux/Actions";
class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = { stars: 0, available: false };
  }

  componentWillReceiveProps(nextProps, preProps) {
    this.setState({ user: nextProps.user });
  }

  handleLogin = () => {
    this.props.history.push({ pathname: "/dashboard" })
    // setTimeout(()=>{
    // },1000)
  };

  render() {
    return (
      <div>
        <h1>Login</h1>
        <button onClick={this.handleLogin}>Login Karo</button>
      </div>
    );
  }
}


//THis Function will get the updated store
const mapStateToProps = state => {
  console.log(state);
  return {
    user: state.authReducers.user
  };
};

const mapDispatchToProps = dispatch => {
  return {
    updateUser: user => dispatch(authActions.updateUser(user)),
    removeUser: () => dispatch(authActions.removeUser())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);
