import React from "react";
import { connect } from "react-redux";
import { authActions } from "../../Redux/Actions";
import DashboardRoutes from "../../Config/Routes/DashboardRoutes";
class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = { stars: 0, available: false };
  }

  componentWillReceiveProps(nextProps, preProps) {
    this.setState({ user: nextProps.user });
  }

  handleLogout = () => {
    this.props.removeUser();
    this.props.history.push({ pathname: "/" });
  };

  handleToggleComponent = () => {
    if (!this.state.toggleComponent) {
      this.props.history.push({ pathname: "/dashboard/Judge" });
    } else this.props.history.push({ pathname: "/dashboard/" });
    this.setState({ toggleComponent: !this.state.toggleComponent });
  };

  render() {
    return (
      <div>
        <h1>Dashboard</h1>
        <DashboardRoutes />
        <button onClick={this.handleToggleComponent}>Toggle</button>
        <button onClick={this.handleLogout}>Logout Man</button>
      </div>
    );
  }
}

//THis Function will get the updated store
const mapStateToProps = state => {
  console.log(state);
  return {
    user: state.authReducers.user
  };
};

const mapDispatchToProps = dispatch => {
  return {
    updateUser: user => dispatch(authActions.updateUser(user)),
    removeUser: () => dispatch(authActions.removeUser())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Dashboard);
