import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import * as DasboardComponents from "../../Components";

const Routes = props => (
  <div>
    <Route
      exact
      path="/dashboard/"
      render={childProps => <DasboardComponents.Kid {...props} />}
    />
    <Route
      exact
      path="/dashboard/Judge"
      render={childProps => <DasboardComponents.Judge {...props} />}
    />
  </div>
);

export default Routes;
